@import "variables.scss";

.testimonial {
  margin: 139px auto 0 auto;
  max-width: 1920px;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__title {
    display: none;
  }

  &__top {
    background-color: #332f30;
    width: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__text { 
    max-width: 55%;
    font-size: 13px;
    font-style: italic;
    padding-top: 55px;
    padding-bottom: 59px;
    line-height: 2;
    letter-spacing: 0.35px;
    color: #929292;
  }

  &__circle {
    width: 101px;
    height: 101px;
    background-color: #2a2a2a;
    border-radius: 50%;
    border-color: #ffffff;
    border: 3px solid #ffffff;
    margin-top: -50px;
  }

  &__marketing {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 11px;
  }
  
  &__marketing-title {
    font-size: 16px;
    color: $main-black-color;
    font-weight: 700;
    letter-spacing: -0.4px;
  }
  
  &__marketing-subtitle {
    font-size: 14px;
    font-style: italic;
    margin-top: 3px;
  }
}

.rating {
  margin-top: 5px;
}

.rating:not(:checked) > input {
  display: none;                   
}

.rating:not(:checked) > label {
  float: right;
  width: 1em;
  overflow: hidden;
  white-space: nowrap;
  cursor: pointer;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.2;
  color: #ddd;
}

.rating:not(:checked) > label:before {
  content: 'â˜… ';
}

.rating > input:checked ~ label {
  color: #332f30;
}

.rating:not(:checked) > label:hover,
.rating:not(:checked) > label:hover ~ label {
  color: #332f30;
}

.rating > label:hover ~ input:checked ~ label {
  color: #332f30;
}

@media (max-width: 1220px) {
  .testimonial {
    margin-top: 100px;

    &__text {
      padding-top: 35px;
    }

    &__top {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}

@media (max-width: 720px) {
  .testimonial__text {
    max-width: 80%;
    padding-top: 20px;
  }
  .testimonial {
    margin-top: 75px;
  }
}

@media (max-width: 540px) {
  .testimonial__text {
    max-width: 90%;
  }
  .testimonial {
    margin-top: 50px;
  }
}